import { blogs } from "@/lib/blogs";
import { notFound } from "next/navigation";
import { Badge } from "@/components/ui/badge";
import { ArrowLeft, Calendar, Clock, User } from "lucide-react";
import Link from "next/link";
import Comments from "@/components/blogs/Comments";

export async function generateMetadata({ params }) {
  const blog = blogs.find((blog) => blog.id === params.blogId);
  
  if (!blog) {
    return {
      title: "Blog Not Found | TuhinBlog",
      description: "The blog post you're looking for couldn't be found."
    };
  }
  
  return {
    title: `${blog.title} | TuhinBlog`,
    description: blog.excerpt,
  };
}

export async function generateStaticParams() {
  return blogs.map((blog) => ({
    blogId: blog.id,
  }));
}

export default function BlogPostPage({ params }) {
  const blog = blogs.find((blog) => blog.id === params.blogId);
  
  if (!blog) {
    notFound();
  }
  
  return (
    <article className="container mx-auto py-12 px-4 max-w-5xl">
      <Link 
        href="/blogs" 
        className="inline-flex items-center gap-2 text-muted-foreground hover:text-primary mb-6"
      >
        <ArrowLeft className="h-4 w-4" />
        <span>Back to blogs</span>
      </Link>
      
      <h1 className="text-4xl font-bold mb-6">{blog.title}</h1>
      
      <div className="flex flex-wrap gap-4 mb-8">
        <div className="flex items-center gap-2 text-muted-foreground">
          <User className="h-4 w-4" />
          <span>{blog.author}</span>
        </div>
        <div className="flex items-center gap-2 text-muted-foreground">
          <Calendar className="h-4 w-4" />
          <span>{blog.date}</span>
        </div>
        <div className="flex items-center gap-2 text-muted-foreground">
          <Clock className="h-4 w-4" />
          <span>{blog.readTime}</span>
        </div>
      </div>
      
      <div className="aspect-video w-full mb-8 overflow-hidden rounded-lg">
        <img
          src={blog.imageUrl}
          alt={blog.title}
          className="w-full h-full object-cover"
        />
      </div>
      
      <div className="prose prose-lg dark:prose-invert max-w-none mb-8">
        {blog.content.split('\n\n').map((paragraph, index) => {
          if (paragraph.startsWith('```') && paragraph.endsWith('```')) {
            const code = paragraph.slice(paragraph.indexOf('\n') + 1, paragraph.lastIndexOf('```'));
            const language = paragraph.slice(3, paragraph.indexOf('\n'));
            
            return (
              <pre key={index} className="bg-muted p-4 rounded-lg overflow-x-auto">
                <code>{code}</code>
              </pre>
            );
          }
          
          return <p key={index}>{paragraph}</p>;
        })}
      </div>
      
      <div className="flex flex-wrap gap-2 mb-8">
        <span className="text-sm font-medium">Tags:</span>
        {blog.tags.map((tag) => (
          <Badge key={tag} variant="secondary">
            {tag}
          </Badge>
        ))}
      </div>
      
      {/* Comments Section */}
      <Comments comments={blog.comments} />
      
      <div className="border-t pt-8 mt-16">
        <h2 className="text-2xl font-bold mb-4">More blog posts</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {blogs
            .filter((b) => b.id !== blog.id)
            .slice(0, 2)
            .map((relatedBlog) => (
              <Link key={relatedBlog.id} href={`/blogs/${relatedBlog.id}`} className="no-underline">
                <div className="flex gap-4 items-start">
                  <div className="w-24 h-24 flex-shrink-0 rounded-md overflow-hidden">
                    <img
                      src={relatedBlog.imageUrl}
                      alt={relatedBlog.title}
                      className="w-full h-full object-cover"
                    />
                  </div>
                  <div>
                    <h3 className="font-medium line-clamp-2">{relatedBlog.title}</h3>
                    <p className="text-sm text-muted-foreground line-clamp-2 mt-1">
                      {relatedBlog.excerpt}
                    </p>
                  </div>
                </div>
              </Link>
            ))}
        </div>
      </div>
    </article>
  );
} 